@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
/* Palette */
:root {
  --default-yellow: #ffc107;
  --default-red: #dc3545;
  --default-black: #343a40;
  --default-white: whitesmoke;
  --default-green: #64bd9c;
}

.default-color-green {
  color: #64bd9c !important;
  color: var(--default-green) !important;
}

.default-color-white {
  color: whitesmoke !important;
  color: var(--default-white) !important;
}

.default-color-red {
  color: #dc3545 !important;
  color: var(--default-red) !important;
}

.default-color-yellow {
  color: #ffc107 !important;
  color: var(--default-yellow) !important;
}
.default-hover-green:hover {
  background-color:#64bd9c !important;
  background-color:var(--default-green) !important;
  color:white !important
}

.default-background {
  background-color: #343a40;
  background-color: var(--default-black);
}
.default-border-yellow {
  border : 1px solid #ffc107;
  border : 1px solid var(--default-yellow);
}

.inherit-background.list-group-item {
  background-color: inherit
}
/* Palette-End */

/* override bootstrap table style*/
.react-bootstrap-table{
  width: 100%;
}
.editor {
  margin: auto;
}

body {
  margin: 0;
  background-color: #64bd9c;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto','Ubuntu','Segoe UI',  'Oxygen', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

tbody {
  background-color: whitesmoke
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Form */
input[type='email'],
input[type='number'],
input[type='search'],
input[type='text'],
input[type='tel'],
input[type='url'],
input[type='password'],
textarea,
select {
  height: 38px;
  padding: 6px 10px; /* The 6px vertically centers text on FF, ignored by Webkit */
  background-color: #fff;
  border: 1px solid #d1d1d1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
}

/* Label */
label,
legend {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
}
label > .label-body {
  display: inline-block;
  margin-left: 0.5rem;
  font-weight: normal;
}

/* Spacing */
input,
textarea,
select,
fieldset {
  margin-bottom: 1.5rem;
}
.YesNoModal .modal-content{
    border-color: #ffc107;
}

.YesNoModal .modal-content .close{
    color : whitesmoke
}
.App {
  text-align: center;
}

.app-header {
  background-color: #282c34;
  /*
    background-image: url("https://image.shutterstock.com/z/stock-photo-table-football-foosball-soccer-game-aka-kicker-abstract-light-400019149.jpg");
  */
  background-image: url("https://c.pxhere.com/photos/2e/b9/game_soccer_table_football_player_figure-69348.jpg!d");
  background-size: cover;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Toastify__toast--success {
    background-color: #64bd9c;
    border : 2px solid #ffc107;
}

